import React, { useEffect, useRef, useState } from "react";
import * as StompJs from "@stomp/stompjs";
import * as SockJS from "sockjs-client";
import './chatbot.css';

const ChatBot = () => {

  const client = useRef({});
  const [chatMessages, setChatMessages] = useState([]);
  const [message, setMessage] = useState("");

  // useEffect(() => {
  //   connect();

  //   return () => disconnect();
  // }, []);

  const connect = () => {

    client.current = new StompJs.Client({

      webSocketFactory: () => new SockJS("/ws")
      ,connectHeaders: {
        "auth-token": "spring-chat-auth-token"
      }
      ,debug: function (str) {
        console.log(str);
      }
      ,reconnectDelay: 5000
      ,heartbeatIncoming: 4000
      ,heartbeatOutgoing: 4000
      ,onConnect: () => {
        subscribe();
      }
      ,onStompError: (frame) => {
        console.error(frame);
      }
    });

    client.current.activate();
  };

  const disconnect = () => {
    client.current.deactivate();
  };

  const subscribe = () => {
    client.current.subscribe(`/topic/public`, function (response) {
        console.log(" : " + response.body); //     
        console.log("response: " + response);
        setChatMessages((_chatMessages) => [..._chatMessages, response.body]);
    });
  };

  const publish = (message) => {
    
    if (!client.current.connected) {
      return;
    }

    setChatMessages((_chatMessages) => [..._chatMessages, message]);

    client.current.publish({
      destination: "/app/sendMessage",
      body: JSON.stringify(message)
    });

    setMessage("");
  };

  const handleOnKeyPress = e => {
    if (e.key === 'Enter') {
      publish(message);
    }
  };

  return (
    <div>
      <div>
        <button onClick={() => connect()}></button>
        <button onClick={() => disconnect()}></button>
      </div>
      <div className="chat_wrap">
          <div className="header">
              CHAT
          </div>
          <div className="chat format">
            {chatMessages && chatMessages.length > 0 && (
              <ul>
                {chatMessages.map((_chatMessage, index) => (
                  <li key={index}>
                    <div className="sender">
                        <span>{index}</span>
                    </div>
                    <div className="message">
                        <span>{_chatMessage}</span>
                    </div>
                  </li>
                ))}
              </ul>
            )}
          </div>
          <div className="input-div">
            <textarea
              type={"text"}
              placeholder={" "}
              value={message}
              onChange={(e) => setMessage(e.target.value)}
              onKeyPress={handleOnKeyPress}
            />
            <button onClick={() => publish(message)}>send</button>
          </div>
      </div>
    </div>
  );
};

export default ChatBot;